            "The map above shows the effects climate is projected to have on food production. Through examination of this map, one can notice quite distinctly that countries in the southern hemisphere will notice a decrease in productivity while countries in the northern hemisphere will actually notice an increase. Developing countries as well as areas in Africa, the Middle East and Central and South America are especially susceptible to losing production. This is concerning as when one compares the two maps, a strong negative correlation can be determined wherein the countries that are already struggling with food production are also the most susceptible to climate effects.",
          ],
          url: "/pages/knowledge-base/category"
        },
        {
          id: 5,
          title: "Risk And Vulnerability",
          description: "Gummi bears pudding icing sweet caramels chocolate",
                              graphic: require("@/assets/images/geo/risk.jpg"),
          info: [
            "Vulnerability - The characteristics and circumstances of a community, system or asset that make it susceptible to the damaging effects of a hazard. Vulnerability can directly affect the risk of a disaster, making the same disaster more destructive in more vulnerable regions of the world.",
            "Risk - The likelihood for loss of life, injury, or destruction and damage from a disaster in a given time period. The risks of a disaster are affected by the magnitude, length, and the vulnerability/resilience of a community to the disaster.",
            "Changing agriculture affects many people in different communities across the globe. Farmers across the globe will be forced to adapt with increasingly intense climate conditions that affect crop yield. As plants and organisms are extremely sensitive to environmental conditions, farmers across the globe will be forced to change the crops they grow in order to maximize productivity. Agricultural productivity is expected to increase north of the 40h parallel, while agricultural productivity will decrease south of the parallel. ",
            "Most of the world’s developing countries are located south of 40th parallel, and many already suffer from widespread food insecurity. They are already considered vulnerable due to food insecurity and low calorie intake. Any losses in productivity could put the country into a famine. This vulnerability significantly increases the risk of natural disasters that are expected to increase in both frequency and magnitude. This perfect storm of economic and geographic factors means that it is the poor and vulnerable that stand to suffer the most from impacts of climate change on agriculture.",
            "Rural farming communities will be disproportionately impacted by climate change, where the main driver of economic activity is agriculture. Unlike urban communities which have significantly greater financial bases and cheap distribution systems, rural communities spend more for delivery and rely more heavily on locally sourced food. Their vulnerability stems from their greater dependence on local goods and the inaccessibility of outside sources. This amplifies the risk of climate change, as natural disasters destroy the community’s main food supply and main economic sector.",
          ],
          url: "/pages/knowledge-base/category"
        },
        {
          id: 6,
          title: "Solutions and Mitigation",
          description:
            "Dragée jelly beans candy canes pudding cake wafer. Muffin croissant.",
          info: [
            "The simplest mitigation strategy involves gradually reducing fossil fuels to zero use. This is mostly targeted toward coal, oil, and natural gas due to their high levels of greenhouse gas emissions, and hopes to replace them with alternative energy sources. Specifically, biomass is an effective way to either make this transition or to even act as the energy source itself.",
            "Agricultural residues, including stems, leaves, husks, roots, and other inutile parts of the plant, can be used as biofuel to produce energy. Seeing as these crops remove carbon dioxide from the air, biomass usually have considerably lesser net carbon emissions per unit energy produced than fossil fuels. As such, even though combustion of biomass is not usually carbon neutral, it provides an effective transition on the way from fossil fuel-powered power plants to completely emission-free energy sources and an effective alternative to simply abandoning or demolishing old coal-fired power plants.",
            "Drax, the largest power plant in the United Kingdom, was opened in 1973 as a completely coal-fired power station. However, since the UK announced its plan to ban all coal-fired power plants by the year 2025, Drax began a plan to convert to a biomass-fueled power plant (which is considerably less carbon positive than coal). This transition began in 2010; currently, 2.6 GW of the station’s 3.9 GW capacity comes from pellet fuel, a form of compressed biofuel partially comprised of agricultural residue. ",
            "A carbon sink is a reservoir to store carbon-containing compounds over a long period of time. Natural sinks include soil (which is estimated to contain 2-3 times as much carbon as the atmosphere), terrestrial plants (such as trees), and oceans. Specifically, the relation between carbon sinks and agriculture mostly concerns the soil.",
            "Agriculture is a major culprit in allowing this stored carbon to escape. Due to deforestation, monoculture, and generally poor farming practice, soil that was once healthy and fertile now suffers from degradation and releases its carbon compounds into the atmosphere. In 2019, the FAO warned that over 90% of all of Earth's soil could be degraded by the year 2050 the current trend continued. Not only is food security directly affected by degraded soils, the release of carbon-containing compounds into the atmosphere could worsen the effects of climate change on Earth and have even wider global impacts.",
            "However, soil can also be used as a medium to contain carbon which was once in the aboveground cycle. If an annual growth rate of 0.4% in soil carbon stocks was reached, the effects of climate change would be significantly reduced. As such, in 2015, the 4 per 1000 initiative was launched, which aims to put policy measures in place to restore agricultural soils and increase the amount of carbon stored in these soils.",
            "Geo-engineering is the deliberate and large-scale intervention in Earth’s natural systems, to counteract the effects of climate change. It is divided into two general categories, greenhouse gas removal (GGR) and solar radiation management (SRM). GGR in agriculture is covered under the carbon sink section; crops and soil can absorb greenhouse gases from the atmosphere and prevent them from helping in the greenhouse gas effect.",
            "In SRM, some of the Sun’s energy is reflected back into space to counteract temperature rise. For instance, albedo enhancement (increasing reflectivity of surfaces) is a common way of ensuring less energy is absorbed by Earth itself. In agriculture, crop plants can be chosen for more reflective leaves, thus decreasing the global temperature (slightly different shapes of crop leaves will reflect different amounts of sunlight). Though this concept remains largely unexplored, SRM is promising in that computer simulations have shown that a simple crop switch to a higher-albedo variety would decrease temperatures in agricultural regions such as North America by as much as 1°C.",
            "This case study shows how farmers in sub-Saharan African LICs have modified their traditional agricultural practices to “climate-smart agriculture.” In order to respond to the challenges of crop growth due to climate change, farmers in Sub-Saharan Africa have employed water-harvesting, capturing surface runoff in order to irrigate crops due to increased frequency and magnitude of dry spells. Furthermore, climate change has also caused the occurrence of stronger winds. To adapt to this, trees have been planted on farms to protect vulnerable crops from strong winds, in addition to providing plants with nutrients through their roots and leaves, and being devices to capture carbon, mitigating global warming. Moreover, farmers are increasingly returning to using manure to fertilize crops, helping maintain the soil’s moisture in order to irrigate crops during periods of decreased rain. Equally, on sloping land, farmers are building terraces to hold soil, water, and nutrients in place, helping stabilize crop production.",
            "The second case study concerns the Peruvian Andes, where climate change has altered the typical annual rain patterns, resulting in degraded soil quality and an inability for crops—such as potatoes and coffee—to sufficiently grow due to lack of water. In order to adapt to these challenges, farmers are now planting potatoes at higher altitudes where climate change has not degraded soil quality, in addition to planting crops later in the year to take full advantage of the altered rain patterns (i.e. from September and October planting to a November and December planting). Moreover, the Peruvian Andes has seen record levels of deforestation to meet agricultural and industrial needs. As trees retain moisture and nutrients, the lack of trees has resulted in insufficient amounts of water present in dry spells to sustain the coffee and potato crops. This results in less crop yield and, therefore, less income for the farmer. In order to adapt to this, reforestation is occurring in vulnerable areas in order to retain water and nutrients and to mitigate the effects of global warming by allowing the newly-planted trees to capture carbon dioxide.",
            "In the third case study, similarly to what has been seen in other locations, climate change has altered the typical monsoon pattern in Cambodia, affecting the growth of crops in the nation due to the resulting increased amount of droughts. As rice is a crop that requires water to grow, reduced crop yields cause significant financial damages to the already-impoverished farmers. In order to adapt to this, Cambodian farmers are growing crops that require less water, such as sesame and bean, which can be grown in plateau areas as they do not require additional irrigation. Furthermore, the Cambodian government is working on creating model rice fields, focusing on rice allowing short-term growth with high yields. Farmers are chanting their rice-planting techniques, with short-term rice production drastically, and substantial decreases in the production of rice requiring the support of the monsoon season to grow (i.e. long-term rice). Equally, Cambodian farmers are also using drip irrigation (a type of micro-irrigation system), which conserves water, money, time, and energy, and can control the timings and magnitude of crop irrigation. Moreover, in lieu of reduced water supply, Cambodians are beginning to raise livestock (i.e. chickens), with chicken-raising skills being especially taught to Indigenous women to improve their livelihoods, raise income for their families, and help sustain their communities. Equally, the Cambodian government has seen the value in forests, having dedicated efforts to their conservation and restoration in order to stabilize this valuable source of water and nutrients.",
            "As in LICs, the climate is also changing elsewhere in HICs, impacting farmers and farming. For the fourth case study, some South Dakotan farmers are barely breaking even, even while focusing on growing conventional crops, such as corn. Some South Dakotan families have decided to switch from conventional to organic farming. The latter involves manual weed scraping in lieu of plowing and spraying them with weed killers; this also involves the fertilization of crops with manure rather than pesticides. As rain no longer comes gradually throughout the year due to the alteration of weather patterns caused by climate change, rain instead comes in fewer albeit larger intervals which the land is unable to soak up. In order to adapt to this, these South Dakotans have turned to minimally-tilled land, enriched with organic materials, such as compost, which tend to absorb more rain and stay moist even through dry spells.",
          ],
          graphic: require("@/assets/images/geo/solutions.jpg"),

          url: "/pages/knowledge-base/category"
        }
      ]
    };
  },
  computed: {
    filteredKB() {
      return this.kb.filter(
        item =>
          item.title
            .toLowerCase()
            .includes(this.knowledgeBaseSearchQuery.toLowerCase()) ||
          item.description
            .toLowerCase()
            .includes(this.knowledgeBaseSearchQuery.toLowerCase())
      );
    }
  },
  mounted() {
    this.$nextTick(() => {
      const swiperTop = this.$refs.swiperTop.swiper
      const swiperThumbs = this.$refs.swiperThumbs.swiper
      swiperTop.controller.control = swiperThumbs
      swiperThumbs.controller.control = swiperTop
    })
  },
  methods: {
    handleHover(hovered) {
        this.isHovered = hovered
      }
  },
  components: {
    swiper,
    swiperSlide
  }
};
</script>

